import React, { useState, useEffect } from "react";
import { LeftOutlined, RightOutlined } from "@ant-design/icons";
import { Input, Tabs, Pagination, Button } from "antd";
import ComPagination from "@/components/comPagination/ComPagination.jsx";
import { useTranslation } from "react-i18next";
import { NavLink, useLocation } from "react-router-dom";
import {
  getnoticeList,
  getnoticedetial,
  noticetype,
} from "../../../api/support";
import moment from "moment";
import "./notice.less";
export default function Notice() {
  const { TabPane } = Tabs;
  const [total, settotal] = useState(0);
  const [list, setlist] = useState([]);
  const [activekey, setactivekey] = useState("1");
  const [isshowdetial, setisshowdetial] = useState(false);
  const [detial, setdetial] = useState({});
  const [noticetypelist, setnoticetypelist] = useState({});
  const [params, setParams] = useState({
    keywords: "",
    page: 1,
    limit: 20,
    orderby: "id",
    sort: "desc",
  });
  //decodeURIComponent 解密

  useEffect(() => {
    getnoticetype();
  }, []);
  useEffect(() => {
    getnoticelists();
  }, [params]);
  useEffect(() => {
    getnoticelists();
  }, [activekey]);
  const getnoticetype = () => {
    noticetype().then((res) => {
      setnoticetypelist(res.data.data.list);
      // setactivekey(res.data.data.list[0].id);
      setTimeout(() => {
        getnoticelists();
      }, 0);

      console.log(res.data.data.list[0].id, activekey, "setnoticetypelist");
    });
  };
  const changetab = (e) => {
    console.log(e, "eee");
    setactivekey(e);
    setisshowdetial(false);
  };
  const onTabClick = (e) => {
    console.log(e, "onTabClick");
    if (e === activekey) {
      getnoticelists();
      setisshowdetial(false);
    }
  };
  const tosearch = (e) => {
    console.log(e, "E");
    setParams({
      ...params,
      keywords: e.target.value,
    });
  };
  const onPressEnter = (e) => {
    getnoticelists(e);
  };
  const getnoticelists = () => {
    let param = {
      addon_idcsmart_news_type_id: activekey,
      // keywords:params.keywords,
      ...params,
    };
    getnoticeList(param).then((res) => {
      settotal(res.data.data.count);
      setlist(res.data.data.list);
      console.log(res, "res");
    });
  };
  const tonoticedetial = (id) => {
    getnoticedetials(id);
    setisshowdetial(true);
  };
  const getnoticedetials = (id) => {
    getnoticedetial({ id }).then((res) => {
      setdetial(res.data.data.news);
    });
  };
  const beforepage = () => {
    getnoticedetials(detial.prev.id);
  };
  const nextpage = () => {
    getnoticedetials(detial.next.id);
  };
  const changePage = (page, limit) => {
    setParams((params) => {
      return { ...params, page, limit };
    });
  };
  const downloadfile = (url) => {
    const downloadElement = document.createElement("a");

    downloadElement.href = url;

    downloadElement.download = url.split("^")[1]; // 下载后文件名

    document.body.appendChild(downloadElement);

    downloadElement.click(); // 点击下载
  };

  return (
    <div className="notice flex  just_center">
      <div className="help_top">
        <div className="bg_right"></div>
        <div className="help_title">我们能帮你什么吗？</div>
        <div className="help_searh flex align_center">
          <img
            src={require("@/assets/images/search.png")}
            onClick={getnoticelists}
          />
          <Input
            placeholder="请输入"
            bordered={false}
            onPressEnter={(e) => onPressEnter(e)}
            onChange={tosearch}
          />
        </div>
        <div className="help_tabs">
          <Tabs
            activeKey={activekey}
            onChange={changetab}
            onTabClick={onTabClick}
          >
            {noticetypelist.length > 0
              ? noticetypelist.map((item) => {
                  return (
                    <TabPane tab={item.name} key={item.id}>
                      {!isshowdetial ? (
                        <div className="station">
                          <div className="station_list">
                            {list.map((it, index) => {
                              return (
                                <div
                                  key={it.id}
                                  className="station_item"
                                  onClick={() => tonoticedetial(it.id)}
                                >
                                  <span style={{ color: "#000" }}>
                                    【
                                    {moment(it.create_time * 1000).format(
                                      "YYYY-MM-DD"
                                    )}
                                    】
                                  </span>
                                  &nbsp;&nbsp;
                                  <span>{it.title}</span>
                                </div>
                              );
                            })}
                          </div>
                          <div className="station_page">
                            <ComPagination
                              total={total}
                              page={params}
                              changePage={changePage}
                            ></ComPagination>
                          </div>
                        </div>
                      ) : (
                        ""
                      )}
                    </TabPane>
                  );
                })
              : ""}
          </Tabs>
          {isshowdetial ? (
            <div className="system">
              <p className="detialtitle">{detial.title}</p>
              <div className="subtimebar flex">
                <div
                  title={moment(detial.create_time * 1000).format("YYYY-MM-DD")}
                >
                  时间：
                  {moment(detial.create_time * 1000).format("YYYY-MM-DD")}
                </div>
                <div className="keywords" title={detial.keywords}>
                  关键字：{detial.keywords}
                </div>
              </div>
              <div
                className="system_con"
                dangerouslySetInnerHTML={{ __html: detial.content }}
              />
              {detial ? (
                <div className="attachment_box">
                  附件：
                  {detial.attachment && detial.attachment.length > 0
                    ? detial.attachment.map((item, index) => {
                        return (
                          <span
                            className="download"
                            onClick={() => {
                              downloadfile(item);
                            }}
                          >
                            {item.split("^")[1]} &emsp;
                          </span>
                        );
                      })
                    : ""}
                </div>
              ) : (
                ""
              )}

              <div className="content_footer flex just_between">
                <div className="footer_page" onClick={beforepage}>
                  {/* &nbsp;<span>上一页</span> &nbsp;&nbsp; */}
                  {JSON.stringify(detial.prev) !== "{}" ? (
                    <div>
                      <LeftOutlined />
                      <span>上一篇文章</span>
                    </div>
                  ) : (
                    ""
                  )}
                </div>

                <div className="footer_page" onClick={nextpage}>
                  {/* <span>下一页</span> &nbsp;&nbsp; */}
                  {JSON.stringify(detial.next) !== "{}" ? (
                    <div>
                      <span>下一篇文章</span>
                      &nbsp;
                      <RightOutlined />
                    </div>
                  ) : (
                    ""
                  )}
                </div>
              </div>
            </div>
          ) : (
            ""
          )}
        </div>
      </div>
    </div>
  );
}
