import React from "react";
import { Pagination } from "antd";
import "./ComPagination.css";
export default function ComPagination(props) {
  const changePage = (page, limit) => {
    props.changePage(page, limit);
  };
  const { page } = props;
  return (
    <Pagination
      total={props.total}
      pageSize={page.limit}
      showSizeChanger
      pageSizeOptions={props.pageSizeOptions || [10, 20, 30, 50, 100]}
      current={page.page}
      showTitle={false}
      onChange={changePage}
      showTotal={(total, range) => `显示${range[0]}-${range[1]} 的 ${total}`}
    />
  );
}
