import request from "../utils/request";
//获取工单列表
export function geTickList(param) {
  return request.get(`/ticket`, { params: { ...param } });
}
//获取工单类型
export function geTickType() {
  return request.get(`/ticket/type `);
}
//获取产品列表
export function getHostList(param) {
  return request.get(`/host`, { params: { ...param } });
}
//创建工单
export function createOrder(param) {
  return request.post("/ticket", param);
}
//工单统计
export function ticketNums() {
  return request.get("/ticket/statistic");
}
//催单
export function onUrge(id) {
  return request.put("/ticket/" + id + "/urge");
}
//关闭
export function onClose(data) {
  return request.put("/ticket/" + data.id + "/close");
}
//工单信息
export function getTickInfo(id) {
  return request.get("ticket/" + id);
}
//回复工单
export function replyMssage(id, param) {
  return request.post("ticket/" + id + "/reply ", param);
}
//帮助文档列表
export function gethelplist(param) {
  return request.get("/help", { params: { ...param } });
}

//帮助中心首页
export function gethelpindex(param) {
  return request.get("/help/index", { params: { ...param } });
}
//帮助文档详情
export function gethelpdetial(param) {
  return request.get("/help/" + param.id, { params: { ...param } });
}
//获取新闻分类
export function noticetype(param) {
  return request.get("/news/type", { params: { ...param } });
}
//新闻列表
export function getnoticeList(param) {
  return request.get("/news", { params: { ...param } });
}
//新闻详情
export function getnoticedetial(param) {
  return request.get("/news/" + param.id, { params: { ...param } });
}
//获取文件夹
export function folderList(param) {
  return request.get("/file/folder", { params: { ...param } });
}
//文件列表
export function fileList(param) {
  return request.get("/file", { params: { ...param } });
}
//下载文件
export function downloadfile(param) {
  return request.get("/file/" + param.id + "/download", {
    params: { ...param },
    responseType: "blob",
  });
}

//获取上传文件数据
export function getDownload(param) {
  return request.post("ticket/download", param, {
    responseType: "blob",
  });
}
